import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import AdminLoginHeader from "../../components/Admin/AdminLoginHeader";
import Input from "../../components/Admin/Input";
import Button from "../../components/Admin/Button";
import axios from "axios";
import DangerAlert from "../../components/Admin/DangerAlert";
import SuccessAlert from "../../components/Admin/SuccessAlert";
function AdminLogin() {
  var apiURL = "https://blueprintcatalyst.com/api/admin/";
  const navigate = useNavigate();
  const [successMessage, setsuccessMessage] = useState("");
  const [dangerMessage, setdangerMessage] = useState("");
  const [userdataa, setuserdataa] = useState("");
  useEffect(() => {
    const storedUsername = localStorage.getItem("adminLogin");
    const userLogin = JSON.parse(storedUsername);

    if (userLogin !== null) {
      navigate("/admin/dashboard");
    }
  }, [userdataa]);

  const handleSubmit = async (event) => {
    event.preventDefault(); // Prevent default form submission

    // Validate email and password
    var vl = event.target;
    const formData = new FormData();
    formData.append("email", vl.email.value);
    formData.append("password", vl.password.value);

    try {
      const res = await axios.post(apiURL, formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      console.log(res.data);

      var userResp = res.data.user;
      let userData = {
        id: userResp.id,
        email: userResp.email,
      };
      localStorage.setItem("adminLogin", JSON.stringify(userData));
      navigate("/admin/dashboard");
      // Handle success (e.g., redirect or show a success message)
    } catch (err) {
      console.log("dd");
      console.error(err);
      if (err.response && err.response.data && err.response.data.message) {
        setdangerMessage(err.response.data.message);
      } else {
      }
    }
  };
  return (
    <div>
      <AdminLoginHeader />
      <section className="d-block admin_loginhero">
        <div className="container-xl h-100">
          {dangerMessage && (
            <div className="pt-3 px-3">
              <DangerAlert
                message={dangerMessage}
                onClose={() => setdangerMessage("")}
              />
            </div>
          )}
          {successMessage && (
            <div className="pt-3 px-3">
              <SuccessAlert
                message={successMessage}
                onClose={() => setsuccessMessage("")}
              />
            </div>
          )}

          <form
            onSubmit={handleSubmit}
            method="post"
            className="d-flex justify-content-center align-items-center h-100"
          >
            <div className="d-flex flex-column gap-3">
              <h1>Admin Login</h1>
              <div className="d-flex flex-column gap-2">
                <Input
                  type="email"
                  required
                  placeholder="Enter Username"
                  name="email"
                />
                <Input
                  type="password"
                  required
                  name="password"
                  placeholder="Enter Password"
                />
              </div>
              <div className="d-flex justify-content-end align-items-end pt-2">
                <Button text="Login" />
              </div>
            </div>
          </form>
        </div>
      </section>
    </div>
  );
}

export default AdminLogin;
